











import { Vue, Component } from 'vue-property-decorator'
import { State, Getter, Action, Mutation, namespace } from 'vuex-class'
// import Store from './module/store'
import { MsgBoxContent, Banner } from './models'
import MsgBoxTools from './mixins/msgBoxTools'
import MessageBox from './components/MessageBox.vue'

const bannersModule = namespace('./module/store')

@Component({
  components: {
    MessageBox
  }
})

export default class ModuleBanners extends MsgBoxTools {
  msgBoxContent: MsgBoxContent = {
    title: 'Сервер не отвечает',
    msg: 'Произошла ошибка при загрузке данных',
    loadBtn: 'Загрузить повторно'
  }

  // get list() { return Store.getters.listSorted }
  @bannersModule.Getter('listSorted') list: Banner[]

  @bannersModule.Action getList

  async created() {
    if (!this.list || !this.list.length) this.loadData()
  }

  async loadData() {
    this.msgBoxIsLoading = true
    // await Store.dispatch('getList')
    await this.getList()
      .then(() => console.log('List loaded!'))
      .catch(async (error) => {
        console.log(error)
        this.onErrorCatch()
      })
  }
}
